@import "config";
@import "modules/mixin";

@include set-html5-elements-block;

$pos-type: static;

.aside {
	.parent {
		@include position($top: 20px); }
	.child {
		@include pos(absolute, -30px, -10px); } }

header {
	@include pos();
	.brand {
		@include pos(absolute, 100px); } }

footer {
	@include pos($position-type: fixed, $left: 0px, $bottom: 0px); }

.gnb li {
	@include float(right, 0 10px 0); }

a {
	@include link-style(#333, 0.012em, 2px solid darkred);

	.test & {
		@include link-style(#25A04D, 0.02em, 4px solid hotpink); } }

a {
	display: inline-block;
	vertical-align: middle;
	padding: 1em 1.4em;
	@include box-shadow(3px 0 4px red, 2px 2px 10px 9px blue, 3px 0 4px red, 2px 2px 10px 9px blue, 3px 0 4px red, 3px 0 4px red, 2px 2px 10px 9px blue, 3px 0 4px red, 2px 2px 10px 9px blue, 3px 0 4px red); }
